import { atom } from 'recoil';

interface PickerTypes {
  id: string | undefined;
  title: any;
  poster_path: any;
}

export const pickerState = atom<PickerTypes[]>({
  key: 'pickerState',
  default: [],
});

export const displayState = atom<boolean>({
  key: 'displayState',
  default: false,
});
